import { createStore } from 'redux';
import './utils/redux-devtools-extension'; // chrome-extension://lmhkpmbekcpmknklioeibfkpmmfibljd/js/redux-devtools-extension.js
import reducers from './reducers';

let store;
if (window.devToolsExtensionID) store = createStore(reducers, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());
else store = createStore(reducers);
export default store;



